* {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
    box-sizing: border-box;
}

body {
    margin: 0 1.5rem;
}

.column-3 {
    width: 75%;
    float: left;
}

.column-2 {
    width: 50%;
    float: left;
}

.column-1 {
    width: 25%;
    float: left;
}

.calc-button {
    width: 100%;
    height: 100px;
    border: 2px solid #ddd;
    background: #eee;
    font-size: 32px;
    color: #555;
}

.calc-button:hover {
    background: #ddd;
}

.result {
    /* border: 2px dotted blue; */
    position: relative;
}

.result--main {
    height: 100px;
    width: 100%;
    border: none;
    border: 2px solid #ddd;
    border-bottom: none;
    text-align: right;
    font-size: 32px;
    padding-right: 1.5rem;
}

.result--small {
    position: absolute;
    text-align: right;
    padding-right: 1.5rem;
    top: 0.75rem;
    right: 0;
    z-index: 99;
    color: #aaa;
    width: 100%;
    /* border: 2px solid red; */
}

.App {
    max-width: 500px;
    margin: 1rem auto;
}